import { animated, useSpring } from "@react-spring/web";
import { useState } from "react";
import styled from "styled-components";
import { DmIcon12Down } from "../../icons";
import { colors } from "../../styles/colors";
import { absoluteFillCSS, flexCSS } from "../../styles/commons";
import { configEaseOutStandard } from "../common";

interface Props {
  expand?: boolean;
  onClick?: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}
export function DropdownBtn({ onClick, expand }: Props) {
  const [hover, setHover] = useState(false);
  const [spring] = useSpring(
    () => ({
      from: { rotate: 0 },
      to: { rotate: expand ? 180 : 0 },
      config: configEaseOutStandard,
    }),
    [expand]
  );
  return (
    <DropdownButtonWrapper
      onClick={onClick}
      onMouseEnter={() => !hover && setHover(true)}
      onMouseLeave={() => hover && setHover(false)}
    >
      <ArrowDown style={spring} />
      {hover && <Overlay />}
    </DropdownButtonWrapper>
  );
}

const DropdownButtonWrapper = styled.div`
  display: flex;
  ${flexCSS.center}
  position: absolute;
  right: 4px;
  top: 4px;
  width: 28px;
  height: 28px;
  border-radius: 2px;
  transition: background-color 150ms ease-out;
  background-color: ${colors.plus_ash};
`;

const Overlay = styled.div`
  ${absoluteFillCSS}
  background: ${colors.addHoveredGradient};
`;

const ArrowDown = styled(animated(DmIcon12Down))`
  width: 12px;
  height: 12px;
`;
