import styled from "styled-components";
import { flexCSS } from "../../styles/commons";
import { typography } from "../themes";

/**
 * @example
 * <Form>
 *   <FormRow>
 *     <FormLabel>Label Name</FormLabel>
 *     <FormContent>
 *       <Select options={options} />
 *     </FormContent>
 *   </FormRow>
 * </Form>
 */
export const Form = styled.div`
  ${flexCSS.column}
  align-items: flex-start;
  gap: 12px;
  width: 100%;
`;

interface IFormRowProps {
  label?: string;
  children?: React.ReactNode;
}
export const FormRow = ({ label, children }: IFormRowProps) => (
  <FormRowWrapper>
    {label && <FormLabel>{label}</FormLabel>}
    {children}
  </FormRowWrapper>
);

export const FormRowWrapper = styled.div`
  ${flexCSS.row}
  align-items: center;
  justify-content: flex-start;
  min-height: 36px;
  width: 100%;
`;

export const FormLabel = styled.span`
  ${typography.dymenu_MenuTitle}
  width: 100px;
  flex-shrink: 0;
`;

export const FormContent = styled.span`
  display: flex;
  gap: 8px;
  width: 100%;
`;
