import {
  DynamicMenuContext,
  ImperativeActorContext,
  configEaseOutStandard,
  configExaggerated,
} from "@orthogonalgroup/ui/src/DynamicMenu";
import { DmIcon20History } from "@orthogonalgroup/ui/src/icons";
import { useContext, useState } from "react";
import styled from "styled-components";

const normal = {
  config: configEaseOutStandard,
};
const ex = {
  config: configExaggerated,
};
export function SimpleIcon() {
  const menuController = useContext(DynamicMenuContext);
  const actorController = useContext(ImperativeActorContext);
  const [pos, setPos] = useState(0);
  const handleClick = () => {
    setPos((pos + 1) % 4);
    switch (pos) {
      case 0:
        menuController.returnToInitialStyle?.();
        actorController.x?.start(0);
        actorController.y?.start(0);
        return;
      case 1:
        menuController.startSizeTo?.({ width: 132, x: 200 });
        actorController.x?.start(80);
        return;
      case 2:
        menuController.scaleSizeTo?.({
          width: 232,
          height: 192,
          y: 20,
          config: ex,
        });
        actorController.x?.start(120, ex);
        actorController.y?.start(140, ex);
        return;
      case 3:
        menuController.startSizeTo?.({
          width: 232,
          height: 232,
          y: 20,
          // config is overwritten by ex, use normal to overwrite both width and height
          config: normal,
        });
        actorController.x?.start(0, normal);
        actorController.y?.start(180, normal);
        return;
    }
  };
  return (
    <Icon
      onClick={handleClick}
      onMouseEnter={() => actorController.opacity?.start(1)}
      onMouseLeave={() => actorController.opacity?.start(0.5)}
    />
  );
}

const Icon = styled(DmIcon20History)`
  width: 40px;
  height: 40px;
  cursor: pointer;
  border-radius: 4px;
`;
